body{-webkit-transition:padding .4s;transition:padding .4s}.push-down{padding-top:38px}.admin-bar-container{position:fixed;top:0;left:0;right:0;margin:0;z-index:1000;-webkit-filter:drop-shadow(0 0 10px #000);filter:drop-shadow(0 0 10px black)}.admin-bar-container .admin-bar-control{position:absolute;top:0;right:0;padding:0 8px;height:38px;line-height:38px;background-color:#c33;color:#3cc;font-size:18px;text-align:center}.admin-bar-container .admin-bar-control:hover{text-decoration:none;font-weight:bolder;color:#ddd}.admin-bar-container .admin-bar-toggler{opacity:.5;border-bottom-left-radius:1rem}.admin-bar-container .admin-bar-inner{display:block;width:100%;height:38px;background-color:#c33;position:relative}.admin-bar-container .hmenu{margin:0;padding:1px 10px;width:auto;list-style:none}.admin-bar-container .hmenu li{float:left;display:block;margin-left:4px;text-align:center;position:relative;line-height:36px}@media (max-width:768px){.admin-bar-container .hmenu li{position:static}.admin-bar-container .hmenu li .sm-dropdown{left:-4px;right:-4px;margin-left:0!important;min-width:0!important}}.admin-bar-container .hmenu li .sm-dropdown{display:none;position:absolute;top:0;margin-top:36px;margin-left:-6px;padding:10px;min-width:500px;line-height:normal;background-color:#fff;border:1px solid #c33;border-top:0;border-radius:5px;text-align:left;z-index:5000}.admin-bar-container .hmenu li .sm-dropdown p{display:block;margin-top:1em}.admin-bar-container .hmenu li .menu-item{padding:10px;color:#ccc;text-transform:uppercase;text-decoration:none;text-shadow:0 1px 0 #000}.admin-bar-container .hmenu li .btn,.admin-bar-container .hmenu li .menu-item{font:700 1.2em Arial,Helvetica}.admin-bar-container .hmenu li .btn{margin:5px 0 5px 5px}.admin-bar-container .hmenu li .dropped,.admin-bar-container .hmenu li:hover>.menu-item{color:#333;background-color:#fff;border-radius:5px}.admin-bar-container .hmenu li .dropped,.admin-bar-container .hmenu li:hover>.dropped{padding-bottom:14px;border-radius:5px 5px 0 0;z-index:1}.admin-bar-container .hmenu li .spacing{width:25px}.admin-bar-container .hmenu .right{float:right;position:relative}.admin-bar-container .ex-positions-box{border:1px solid #ccc;border-radius:4px;max-height:85px;padding:4px 0;overflow:auto}.admin-bar-container .ex-positions-box label{display:block;padding:0 5px;margin:0}.admin-bar-container .ex-positions-box label:hover{background-color:#ee3}.message-container{position:relative;left:50%;float:left;clear:both;font-size:1.2em;text-align:left}.message-container .flash-message{display:none;background-color:#d5f8a0;padding:5px 10px;position:relative;left:-50%;text-align:left}.block-admin-button-active{background:#fff none;border-bottom:0;z-index:1000}